.credit-card-front {
  background: url("https://raw.githubusercontent.com/muhammederdem/credit-card-form/master/src/assets/images/5.jpeg");
  background-position: center center;
  background-size: cover;
  border-radius: 10px;
  box-shadow: 5px 10px 5px #ccc;
  color: white;
  display: flex;
  flex-direction: column;
  height: 270px;
  padding: 10px;
  width: 100%;
}

.credit-card-chip-brand-container {
  display: flex;
  justify-content: space-between;
}

.credit-card-name-and-expiration-container {
  align-items: center;
  display: flex;
}

.credit-card-name-container,
.credit-card-expiration-container {
  width: 50%;
}

.credit-card-number-container {
  align-items: center;
  display: flex;
  flex: 1;
  justify-content: center;
}


.credit-card-chip-brand-container,
.credit-card-name-and-expiration-container {
  height: 100px;
}

.credit-card-chip-container,
.credit-card-logo-container {
  width: 50%;
}

.credit-card-chip,
.credit-card-logo {
  max-height: 100%;
  max-width: 100%;
}

.credit-card-name-container,
.credit-card-expiration-container,
.credit-card-number {
  cursor: pointer;
}

.credit-card-holder-label,
.credit-card-expiration-label {
  font-size: 14px;
}

.credit-card-number,
.credit-card-holder-name,
.credit-card-expiration {
  font-size: 20px;
}